a,
.control {
  color: #99225c;
}
.bfloat > svg {
  fill: #99225c;
}
.bmodal hr {
  border-top: 1px solid #99225c;
}
#banner {
  background: rgba(46, 46, 46, 0.7);
  border-bottom: 1px solid #262626;
}
b {
  color: #808080;
}
b.admin {
  color: #dc322f;
}
b.moderator {
  color: #5f5faf;
}
blockquote a {
  color: #99225c;
}
a:hover {
  color: #af005f;
}
article,
aside,
.pagination {
  background-color: rgba(46, 46, 46, 0.95);
  border-right: 1px solid #262626;
  border-bottom: 1px solid #262626;
}
article.highlight {
  background-color: rgba(44, 44, 51, 0.95);
  border-color: #21212c;
}
article.editing {
  background-color: rgba(51, 41, 41, 0.95);
  border-color: #2c2121;
}
body {
  color: #999999;
  background-color: #1e1e1e;
  background-image: url('higan.jpg');
  background-repeat: no-repeat;
  background-size: contain;
  background-attachment: fixed;
  background-position: right bottom;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 10pt;
}
pre,
.themed {
  color: #8a8a8a;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 10pt;
}
em {
  color: #859900;
}
h1 {
  color: #731a19;
  font-family: "MS Pゴシック", "MS PGothic", "Mona", "Hiragino Kaku Gothic Pro", Helvetica, sans-serif;
  font-size: 30px;
  text-shadow: #590030 1px 1px 0px;
  letter-spacing: 1px;
}
h2 {
  color: #731a19;
  font-family: Tahoma;
}
h3 {
  color: #ab2b16;
}
header nav a {
  color: #999999;
}
hr {
  border-top: 1px solid #2e2e2e;
}
.popup-menu li:hover {
  background-color: #e4e4e4;
}
.email {
  color: #99225c;
}
.embed {
  background-color: #e4e4e4;
}
.modal,
.bmodal,
.popup-menu {
  background-color: #262626;
  border-right: 1px solid #212121;
  border-bottom: 1px solid #212121;
}
.omit {
  color: #626262;
}
#lock {
  background-image: url('ui/invlock.svg');
  background-size: 1em 1em;
}
.option_tab_sel a.tab_sel {
  color: #8B3FB1;
}
.themed {
  color: #8a8a8a;
}
